
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

/*
实现 滑动动画效果思路
1. 定义router 权重 ， 定义动画
2. 监听 $route 变化 ， 根据权重 判断 执行 向前进 还是 后退动画
3. 执行动画的外层 容器 需要 position: absolute; 不然会有 白边
*/
export default {
components: {
Tabbar,
TabbarItem,
Group,
Cell,
},
data() {
return {
transitionName : '',
active : -1,
}
},
watch: {
'$route' (to, from) {
if(to.meta.weight && from.meta.weight){
this.transitionName = to.meta.weight > from.meta.weight ? 'forward' : 'reverse';
}else{
this.transitionName = '';
}
}
},
mounted() {

},
}



